import React from 'react';
import {
  Image,
  SafeAreaView,
  ScrollView,
  Text,
  TouchableOpacity,
  View,
} from 'react-native';
import Animated, {useSharedValue, withTiming} from 'react-native-reanimated';

export default () => {
  const size = useSharedValue(100);

  return (
    <SafeAreaView>
      <ScrollView>
        <View style={{marginTop: 108}}>
          <TouchableOpacity
            style={{
              backgroundColor: '#d3d3d3',
              borderRadius: 4,
              marginBottom: 24,
            }}
            onPress={() => {
              size.value = withTiming(size.value + 50);
            }}>
            <Text>Click</Text>
          </TouchableOpacity>

          <Animated.View
            style={{
              height: 50,
              width: size,
              backgroundColor: 'red',
            }}></Animated.View>
        </View>
      </ScrollView>
    </SafeAreaView>
  );
};
